@import "../main.scss";
.menuLink {
    color: $blue-color;
    text-decoration: none;
    border: none;
    background-color: transparent;
    font-weight: bold;
    position: relative;
    display: inline-block;
    &:hover {
        &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            bottom: -4px;
            left: 0;
            background-color: $orange-color;
            border-radius: 50%;
        }
    }
}
.button {
    background-color: $blue-color;
    border: none;
    color: $white-color-09;
    padding: 10px;
    border-radius: 3px;
    font-weight: 700;
    transition: 0.8s;
    &:hover {
        background-color: $white-color-05;
        color: $blue-color;
    }
}
@include respond(large) {
    .centerLarge {
        margin-left: auto;
        margin-right: auto;
    }
}
